/**
 * core
 */
.flow {
  position: relative;
  width: 90%;
  height: 500px;
  overflow: hidden;
  margin: 100px 5%;
}
.flow-slide {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  display: none;
  overflow: hidden;
  transition: 0.6s ease-in-out;
  background-position: center;
  background-size: cover;
}
.flow-slide video,
.flow-slide iframe {
  width: 100%;
  height: 100%;
}
.flow-description {
  position: absolute;
  top: 40%;
  left: calc(50% - 200px);
  width: 400px;
  margin: auto;
  text-align: center;
  text-shadow: 1px 2px 4px rgba(119,119,119,0.8);
  color: #fff;
  padding: 10px;
  transition: 0.6s;
  user-select: none;
  cursor: default;
}
.flow-image {
  display: none;
}
.flow-dots {
  margin: 0;
  padding: 0;
  position: absolute;
  bottom: 30px;
  left: 50%;
  display: flex;
  z-index: 100;
  list-style: none;
}
.flow-dots li:first-child {
  margin-left: -50%;
}
.is-vertical .flow-dots {
  display: flex;
  flex-direction: column;
  left: auto;
  right: 40px;
  top: 40%;
  bottom: auto;
}
.is-vertical .flow-dots li:first-child {
  margin: 0;
}
.flow-dot {
  display: block;
  margin: 10px;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background-color: #fff;
}
.flow-dot.is-active {
  background: #3c83f2;
}
.flow-loader {
  position: absolute;
  z-index: 10;
  height: 3px;
  box-shadow: 0 2px 4px -2px rgba(119,119,119,0.9);
  top: 0;
  left: 0;
  background-color: rgba(255,255,255,0.9);
}
.flow:hover .flow-loader {
  opacity: 0;
}
.flow-next,
.flow-back {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 30px;
  top: 0;
  z-index: 200;
  height: 100%;
  fill: #fff;
  color: #fff;
}
.flow-next:hover,
.flow-back:hover {
  fill: #3c83f2;
  color: #3c83f2;
}
.flow-next {
  right: 0;
}
.is-vertical .flow-next {
  transform: rotate(90deg);
  height: auto;
  left: auto;
  top: auto;
  right: 0;
  bottom: 0;
}
.flow-back {
  left: 0;
}
.is-vertical .flow-back {
  transform: rotate(90deg);
  height: auto;
  left: auto;
  right: 0;
}
.flow-fading.is-entering,
.flow-fading.is-leaving {
  opacity: 0;
}
.flow-fading.is-active {
  opacity: 1;
}
.flow-fading.is-active .flow-description {
  opacity: 1;
  transform: translate(0, 0);
}
.flow-fading .flow-description {
  opacity: 0;
  transform: translate(0, -100%);
  transform-origin: center top;
}
.flow-sliding.is-entering {
  transform: translate3d(-100%, 0, 0);
}
.flow-sliding.is-leaving {
  transform: translate3d(100%, 0, 0);
}
.flow-sliding.is-active {
  transform: translate3d(0, 0, 0);
}
.flow-falling.is-entering {
  transform: translate3d(0, -100%, 0);
}
.flow-falling.is-leaving {
  transform: translate3d(0, 100%, 0);
}
.flow-falling.is-active {
  transform: translate3d(0, 0, 0);
}
.is-entering,
.is-leaving,
.is-active {
  display: block;
}
.is-entering .flow-description,
.is-leaving .flow-description,
.is-active .flow-description {
  transition-delay: 0.6s;
}
.cube {
  top: 0;
  left: 0;
  position: absolute;
  height: 100%;
  width: 100%;
  transform-style: preserve-3d;
  transform-origin: center center;
  transition: 0.5s ease;
}
.cube-face {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  transition: 0.5s;
  background-color: #777;
  background-position: inherit;
  background-size: inherit;
  background-repeat: no-repeat;
  transform-origin: center center;
}
.animation-3d {
  position: relative;
  display: none;
  width: 100%;
  height: 100%;
  perspective: 1200px;
  perspective-origin: center center;
}
